<template>
	<section class="panel" id="queForm">
		<div class="title">
			<span>添加试题</span>
			<div class="pull-right">
				<el-button type="success" @click="onSubmit('form')" class="el-button-shadow">保存</el-button>
				<el-button type="danger" @click="resetForm('form')" class="el-button-shadow">取消</el-button>
			</div>
		</div>

		<div class="content">
			<el-form :model="form" :rules="rules" ref="form" label-width="110px" :inline-message="isInlineMessage" @submit.prevent="onSubmit">
				<el-form-item label="所属年级:" prop="grade" :rules="[{required: true, message: '请选择所属年级', trigger: 'change'}]">
					<el-select v-model="form.grade" placeholder="请选择所属年级">
						<template v-for="item in gradeArr">
							<el-option :label="item.name" :value="item.id"></el-option>
						</template>
					</el-select>
				</el-form-item>
				<el-form-item label="所属院系:" prop="department" :rules="[{required: true, message: '请选择所属院系', trigger: 'change'}]">
					<el-select v-model="form.department" placeholder="请选择所属院系">
						<template v-for="item in departmentArr">
							<el-option :label="item.name" :value="item.id"></el-option>
						</template>
					</el-select>
				</el-form-item>
				<el-form-item label="所属班级:" prop="class" :rules="[{required: true, message: '请选择所属班级', trigger: 'change'}]">
					<el-select v-model="form.class" placeholder="请选择所属班级">
						<template v-for="item in classArr">
							<el-option :label="item.name" :value="item.id"></el-option>
						</template>
					</el-select>
				</el-form-item>
				<el-form-item label="学生学号:" prop="studentNo" :rules="[{required: true, message: '请输入学生学号', trigger: 'blur'}]">
					<el-input v-model="form.studentNo" placeholder="请输入学生学号"></el-input>
				</el-form-item>
				<el-form-item label="学生姓名:" prop="name" :rules="[{required: true, message: '请输入学生姓名', trigger: 'blur'}]">
					<el-input v-model="form.name" placeholder="请输入学生姓名"></el-input>
				</el-form-item>
				<el-form-item label="学生性别:" prop="sex" :rules="[{required: true, message: '请选择学生性别', trigger: 'change'}]">
					<el-select v-model="form.sex" placeholder="请选择学生性别">
						<el-option label="男" value="0"></el-option>
						<el-option label="女" value="1"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="登录帐号:" prop="account" :rules="[{required: true, message: '请输入登录帐号', trigger: 'blur'}]">
					<el-input v-model="form.account" placeholder="请输入登录帐号"></el-input>
				</el-form-item>
				<el-form-item label="初始密码:" prop="password" :rules="[{required: true, message: '请输入初始密码', trigger: 'blur'}]">
					<el-input v-model="form.password" placeholder="请输入初始密码"></el-input>
				</el-form-item>
			</el-form>
		</div>
	</section>

</template>

<script>
	import {
        getDepartmentList,
		getClassList,
	} from '../../../api/api';
	export default {
		data() {
			return {
                form: {
                    grade: '',
                    department: '',
                    class: '',
                    studentNo: '',
                    name: '',
                    sex: '',
                    account: '',
					password: '',
                },
                rules: {},
                isInlineMessage: true,
				// 默认数据
                gradeArr: [{
                    id: '0',
					name: '14级',
				},{
                    id: '1',
					name: '15级',
				},{
                    id: '2',
					name: '16级',
				},{
                    id: '3',
					name: '17级',
				}],
                classArr: [],
				departmentArr: [],
			}
		},
		methods: {
            onSubmit(formName) {
                console.log(formName);
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                // this.$refs[formName].resetFields();
				console.log('cancel');
            },
			// 获取初始数据
			getDefaultData() {
                getDepartmentList({}).then((res) => {
                    this.departmentArr = res.data;
                    console.log(res);
                });
                getClassList({}).then((res) => {
                    this.classArr = res.data;
                    console.log(res);
                });
			},
			// 上传文件相关
            handleRemoveContent(file, fileList) {
                console.log(file, fileList);
            },
            handlePreviewContent(file) {
                console.log(file);
            },
			handleRemove(file, fileList, type) {
                console.log(file, fileList, type);
            },
            handlePreview(file, type) {
                console.log(file, type);
            },
			// 添加选项
            selectAddFunc() {
                this.selectNum = this.selectNum + 1;
                const str = String.fromCharCode(64 + this.selectNum);
                this.correctRadioArr.push({ id: this.selectNum - 1, name: str });
                this.correctManyArr.push(str);
                this.rules = this.getRuleArr(this.selectNum);
			},
            // 删除选项
            delSelection(index) {
                if (this.selectNum === 2) {
                    return;
				}
                console.log('del selection', index);
                this.selectNum = this.selectNum - 1;
                this.correctManyArr = this.getCorrectArrByNum(this.selectNum, 'many');
                this.correctRadioArr = this.getCorrectArrByNum(this.selectNum, 'radio');
                this.form.selection.splice(index, 1);
                this.form.selectionPic.splice(index, 1);
                this.rules = this.getRuleArr(this.selectNum);
			},
			getCorrectArrByNum(num, type) {
                const arr = [];
                for(let i= 0;i< num;i++){
                    const str = String.fromCharCode(65 + i);
                    if (type === 'radio') {
                        arr.push({id: i, name: str});
					} else {
                        arr.push(str);
					}
				}
				return arr;
			},
            getRuleArr(num) {
                const arr = [];
                for (let i = 1; i< num; i++) {
                    const str = String.fromCharCode(65 + i);
                    arr[`selection${str}`] = [
                            { required: true, message: `请输入试题选项${str}`, trigger: 'blur' }
                        ];
                }
                const result = {
                    ...arr,

                };
                console.log('rules', result);
                return result;
            }
		},
        computed: {
            isShowSelectionC() {
                return this.selectNum > 2;
            },
            isShowSelectionD() {
                return this.selectNum > 3;
            },
            isShowSelectionAdd() {
                return !(this.selectNum > 3 && this.form.type === '0');
			},
            isRadio() {
                return this.form.type === '0';
			},
			isCheckbox() {
                return this.form.type === '1';
			},
            isJudgment() {
                return this.form.type === '2';
            },
		},
        mounted() {
            this.getDefaultData();
        }
	}

</script>

<style scoped lang="scss">
	@import '~scss_vars';
	#queForm{
		.spec{
			.el-form-item__label{
				line-height: 50px;
			}
		}
	}

</style>


